import { HTMLAttributes, useEffect, useState } from 'preact/compat';
import { classes, Container } from '@adobe-commerce/elsie/lib';
import { events } from '@adobe-commerce/event-bus';
import { ProductModel } from '@/pdp/data/models';
import { getFetchedProductData } from '@/pdp/api';

export interface ProductShortDescriptionProps
  extends HTMLAttributes<HTMLDivElement> { }

export const ProductShortDescription: Container<
  ProductShortDescriptionProps,
  ProductModel | null
> = ({ initialData = null, children, ...props }) => {
  const [data, setData] = useState<ProductModel | null>(initialData);

  useEffect(() => {
    const dataEvent = events.on('pdp/data', setData);

    return () => {
      dataEvent?.off();
    };
  }, []);

  if (!data) return null;

  return (
    <div
      // eslint-disable-next-line react/no-danger
      dangerouslySetInnerHTML={{ __html: data?.shortDescription ?? '' }}
      {...props}
      className={classes(['pdp-short-description', props.className])}
    />
  );
};

ProductShortDescription.getInitialData = getFetchedProductData;
